import React from 'react';
import {BrowserRouter as Router, Switch, Route, Link} from "react-router-dom";
import './App.css';
import Quiensoy from './components/Quiensoy';
import Formacion from './components/Formacion';
import Hobbi from './components/Hobbi';

function App() {
  return (
    <Router>
      <div className="App">
        <div className="menu">
          <Link to="/" className="opcion">
            Inicio
          </Link>
          <Link to="/quiensoy" className="opcion">
            Quien Soy
          </Link>
          <Link to="/formacion" className="opcion">
            Formacion
          </Link>
          <Link to="/hobbi" className="opcion">
            Hobbies
          </Link>
        </div>
        <Switch>
          <Route path="/quiensoy">
            <Quiensoy />
          </Route>
          <Route path="/formacion">
            <Formacion />
          </Route>
          <Route path="/hobbi">
            <Hobbi />
          </Route>
          <Route path="/">
            Ruta raiz
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
